<template>
    <div class ="chat">
        <h1 class="header">VueChat</h1>
        <div class="chat-box" id="chatBox">
            <chat-message 
                v-for="entry of messages" :key="entry._id" 
                v-bind:name="entry.name"
                v-bind:message="entry.message"
                v-bind:time="entry.time"
                v-bind:deleteMessage="deleteMessage"
                v-bind:id="entry._id"
            />
        </div>
        <chat-input v-on:update-messages="updateMessages()"/>
    </div>
</template>

<script>
import ChatMessage from './ChatMessage'
import ChatInput from './ChatInput'
import PostService from '../services/PostService'

export default {
    name: 'Chat',
    components: {
        'chat-message': ChatMessage,
        'chat-input': ChatInput,
    },
    data () {
        return ({
            name: '',
            message: '', 
            messages: [ ]
        })
    },
    sockets: {
        connect: function() {
        },
        messageEmit: function(message) {
            this.messages.push(message);
        },
        deleteEmit: function() {
            this.updateMessages();
        }
    },
    methods: {
        async updateMessages() {
            this.messages = await PostService.getAllMessages();
        },
        deleteMessage(id) {
             this.$socket.emit("delete", id);
        }

    },
    async created(){
        this.messages = await PostService.getAllMessages();
    },
}
</script>
