<template>
  <form class="input" autocomplete="off">
    <input class="name" id="name" type="text" name="name" v-model="name" placeholder="Name">
    <label for="name" class="lname">Name</label>
    <input
      id="message"
      class="message"
      type="text"
      v-model="message"
      ref="message"
      v-on:keyup.enter="addMessage()"
      placeholder="Message"
    >
    <label for="message" class="lmessage">Message</label>
    <input type="button" class="send" id="send" @click="addMessage()" value="Send">
  </form>
</template>

<script>

export default {
  name: "ChatInput",
  data() {
    return {
      name: "",
      message: ""
    };
  },
  sockets: {
    connect: function() {
    },
  },
  methods: {
    addMessage() {
      const message = {
        name: this.name,
        message: this.message,
        time: this.getTimeStamp()
      };
      if(this.message !== "") {
        this.$socket.emit("message", JSON.stringify(message));
        this.message = "";
        this.$refs.message.focus();
      }
    },
    getTimeStamp() {
      let date = new Date();
      return (
        this.formatTime(date.getHours()) + ":" + this.formatTime(date.getMinutes()) + ":" + this.formatTime(date.getSeconds())
      );
    },
    formatTime(time) {
      return time < 10 ? "0" + time : time;
    },
    updateMessages() {
      this.$emit("update-messages");
    }
  }
};
</script>

<style scoped>
form {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: minmax(1vh, auto) minmax(5vh, auto) minmax(1vh, auto) minmax(
      5vh,
      auto
    ) minmax(5vh, auto);
  grid-template-areas:
    "lname ... ........ ........."
    "name name ........ ........."
    "lmessage . ....... ........."
    "message message message send";
  row-gap: 1vh;
}

.name {
  grid-area: name;
}

.lname {
  grid-area: lname;
}

.message {
  grid-area: message;
}

.lmessage {
  grid-area: lmessage;
}

.send {
  margin-left: 2px;
  grid-area: send;
  background-color: #42b883;
  border: 2px solid #42b883;
}

label {
  float: right;
}

@media only screen and (min-width: 601px) {
  form {
    grid-template-areas:
      "lname ... ........ ........."
      "name name ........ ........."
      "lmessage . ....... ........."
      "message message message send";
  }
}

@media only screen and (max-width: 600px) {
  form {
    grid-template-rows: minmax(1vh, auto) minmax(5vh, auto) minmax(1vh, auto) minmax(
        5vh,
        auto
      ) minmax(5vh, auto) minmax(5vh, auto);
    grid-template-areas:
      "lname ... ........ ........."
      "name name ........ ........."
      "lmessage . ....... ........."
      "message message message message "
      "send send send send";
  }
}
</style>
