<template>
    <div class="chat-message">
                <h4>{{name}}</h4>
                <input type="button" value="X" class="deleteMessage" @click="deleteMessage(id)" />
                <p>{{message}}</p>
                <small>{{time}}</small>
    </div>
</template>

<script>
export default {
    name: 'ChatMessage',
    props: {
        name: {
            type: String,
            required: true
        },
        message: {
            type: String,
            required: true
        },
        time: {
            type: String,
            required: true
        },
        id: {
            type: String,
            required: true
        },
        deleteMessage: {
            type: Function,
            required: true
        }
    }
}
</script>

<style scoped>
.chat-message {
    display: grid;
    grid-template-columns: 15px auto auto auto auto;
    grid-auto-rows: minmax(1vh, auto);
    grid-template-areas: 
                        ". by by ...... del"
                        ". txt txt txt ...."
                        ". ... ... ... time";
                        
    margin: 1vh 0 1vh 0;
    background-color: #465a6f;
    border-radius: 0.5vh;
    grid-row-gap: 5px;
}

h4 {
    grid-area: by;
    margin: 5px 0 5px 0;
    color: #42b883;
}
p {
    grid-area: txt;
    margin: -0.25vh 0 -0.25vh 0;
}
small {
    grid-area: time;
    text-align: right;
    margin-right: 5px;
    color: lightgray;
    font-size: 0.5em;
    margin-bottom: 5px;
}

.deleteMessage {
    grid-area: del;
    border-radius: 0.5vh;
    color: #35495e;
    background-color: #f95969;
    border: #f95969;
    text-align: center;
    margin-right: 0px;
    justify-self: end;
    width: 3em;
}
</style>

