import axios from 'axios';

const url = 'api/messages';

class PostService {

    static getAllMessages() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url);
                const messages = res.data;
                resolve(messages);
            } 
            catch (err) {
                reject(err);
            }
        });
    }
}

export default PostService;